<template>
  <div class="grid-box max_width">
    <main-title :thisPage=thisPage @toSearch='toSearch' :hideSearch=hideSearch></main-title>
    <div class="contentBox">
      <div class="listBoxTotal" v-if="listShow" :class="{listHide:listHide}">
        <view class="videoBox">
          <video v-if="playerObj.class==2" id="showVideoBox2" objectFit='fill' :src="src_fix" controls :class="{fixedVideo:fixed}" fixed="true"></video>
          <video v-if="playerObj.class==3" id="showVideoBox3" objectFit='fill' :src="playerObj.video_url" controls :class="{fixedVideo:fixed}" fixed="true" :autoplay='autoplay'></video>
          <txv-video v-if="playerObj.class ==1 " :vid="playerObj.video_id" playerid="showVideoBox1" objectFit='fill'>
          </txv-video>
        </view>
        <div class="listBox grid-var" :style={height:scrollHeight_fix}>
          <scroll-view class="content" scroll-y="true">

            <text-card :video='playerObj'></text-card>

            <div :key=key v-for="(x,key) in dataList">
              <video-card :videos=x v-if='playerObj.id != x.id' @toVideo='toVideo' :index='-1' :leftNone=leftNone></video-card>

            </div>
          </scroll-view>

        </div>
      </div>
      <div class="searchBox" v-if="listHide">
        <search-box :searchPage=thisPage :searchNew=searchNew></search-box>
      </div>
    </div>

  </div>

</template>

<script>
import mainTitle from "@/components/mainTitle";
import videoCard from "@/components/videoCard";
import textCard from "@/components/textCard";
import searchBox from "@/components/searchBox";

export default {
  data() {
    return {
      thisPage: "player",
      prePage: undefined,
      leftNone: true,
      fixed: false,
      scrollHeight: "500px",
      dataList: [],
      // src: "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4",
      // src: "http://static.video.qq.com/TPout.swf?vid=w0766f4ngw3&auto=1",
      src: "",
      listShow: true,
      listHide: false,
      hideSearch: false,
      searchNew: 0,
      videoContext: {},
      vid: "w0766f4ngw3",
      playerObj: {},
      autoplay: false,
      title: "",
      shareImg: ""
    };
  },

  components: {
    mainTitle,
    videoCard,
    textCard,
    searchBox
  },
  computed: {
    src_fix() {
      if (this.playerObj.class == 2) {
        return `${wx.getStorageSync("media_url")}/${this.playerObj.video_url}`;
      } else {
        return ``;
      }
    },
    scrollHeight_fix() {
      var rule = 750 / wx.getSystemInfoSync().windowWidth;
      var height = wx.getSystemInfoSync().windowHeight;
      this.scrollHeight = `${height * rule - (103 + 423)}rpx`;
      return `${height * rule - (103 + 423)}rpx`;
    }
  },

  methods: {
    escape2Html(str) {
      str = str || "";
      var arrEntities = { lt: "<", gt: ">", nbsp: " ", amp: "&", quot: '"' };
      return str.replace(/&(lt|gt|nbsp|amp|quot);/gi, function(all, t) {
        return arrEntities[t];
      });
    },
    toSearch() {
      this.hideSearch = true;
      this.listHide = true;
      setTimeout(() => {
        this.searchNew = +new Date();

        this.listShow = false;
      }, 500);
    },
    getList(bol) {
      var that = this;
      wx.getNetworkType({
        success: function(res) {
          if (res.networkType == "wifi") {
            that.autoplay = true;
            if (that.playerObj.class == 1) {
              var TxvContext = requirePlugin("myPlugin");
              var txvContext = TxvContext.getTxvContext("showVideoBox1");
              txvContext.play();
              console.log("auto1");
            } else if (that.playerObj.class == 2) {
              that.videoContext = wx.createVideoContext("showVideoBox2");
              that.videoContext.play();
              console.log("auto2");
            } else {
              that.videoContext = wx.createVideoContext("showVideoBox3");
              that.videoContext.play();
              console.log("auto3");
            }
          }
        }
      });
    },
    toVideo(x) {
      wx.setStorage({
        key: "video",
        data: JSON.stringify(x)
      });
      wx.setStorageSync("pre_page", this.thisPage);
      wx.setStorageSync("player", {
        data: this.dataList,
        video: x
      });
      var data = wx.getStorageSync("player");
      this.dataList = data.data;
      this.playerObj = data.video;
      this.title =
        this.escape2Html(data.video.typeTitle) ||
        this.escape2Html(data.video.title);
      this.shareImg = `${wx.getStorageSync("media_url")}/${
        data.video.image_url
      }`;
      wx.setStorageSync("pre_page", "none");
      wx.setNavigationBarTitle({
        title: this.title //页面标题为路由参数
      });

      wx.setStorageSync("pre_page", "none");
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .post(`${wx.getStorageSync("url")}/uploadProductStatic`, {
          productId: this.playerObj.id
        })
        .then(d => {
          //输出请求数据
          console.log("req", d.data);
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
      this.getList(true);
    },
    exit() {
      // var SP = wx.getStorageSync("search_page");
      // console.log(SP);
      // if (SP) {
      //   wx.setStorageSync("search_page", false);
      //   return;
      // }
      this.leftNone = false;
      this.listShow = true;
      this.listHide = false;
      this.dataList = [];
      this.hideSearch = false;

      this._watchers = [];
    },
    showStart() {
      wx.showShareMenu({
        withShareTicket: false,
        success: () => {},
        fail: () => {},
        complete: () => {}
      });
      this.prePage = wx.getStorageSync("pre_page");
      if (this.prePage == "none") {
        this.animation = false;
      } else {
        this.exit();
      }
      var data = wx.getStorageSync("player");
      this.dataList = data.data;
      this.playerObj = data.video;
      this.title =
        this.escape2Html(data.video.typeTitle) ||
        this.escape2Html(data.video.title);
      this.shareImg = `${wx.getStorageSync("media_url")}/${
        data.video.image_url
      }`;
      wx.setStorageSync("pre_page", "none");
      wx.setNavigationBarTitle({
        title: this.title //页面标题为路由参数
      });

      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .post(`${wx.getStorageSync("url")}/uploadProductStatic`, {
          productId: this.playerObj.id
        })
        .then(d => {
          //输出请求数据
          console.log("req", d.data);
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
      this.getList();
    },
    showDetail_search(options) {
      console.log("showDetail_search");
      var x = options.key1;
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      wx.setStorageSync("searchText", x);
      fly
        .get(`${wx.getStorageSync("url")}/search`, {
          keyword: x
        })
        .then(d => {
          //输出请求数据
          console.log("req", d.data);

          wx.setStorage({
            key: "goods",
            data: JSON.stringify(x)
          });

          this.share_toVideo(options, d.data.data.list);
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
    },
    showDetail_explain(options) {
      console.log("showDetail_explain");
      var x = {
        id: options.key1,
        title: options.title
      };
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .get(`${wx.getStorageSync("url")}/type/${x.id}`, {})
        .then(d => {
          //输出请求数据
          wx.setStorage({
            key: "goods",
            data: JSON.stringify(x)
          });
          this.share_toVideo(options, d.data.data.list);
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
    },
    share_toVideo(options, list) {
      console.log(list)
      var x_id = options.key2;
      var x = {};
      for (var i = 0; i < list.length; i++) {
        if (x_id == list[i].id) {
          var x = list[i];
        }
      }
      wx.setStorage({
        key: "video",
        data: JSON.stringify(x)
      });
      wx.setStorageSync("pre_page", this.thisPage);
      wx.setStorageSync("player", {
        data: list,
        video: x,
        page: "explain"
      });
      wx.setStorageSync("share_player", {
        video: x
      });
      this.showStart();
    }
  },

  created() {},
  onLoad(options) {
    console.log("option", options);

    if (options.share) {
      wx.setStorageSync("share_player", options.page);
      wx.setStorageSync("share_player_list", options.key1);
      wx.setStorageSync("share_player_video", options.key2);
      if (options.page == "explain") {
        this.showDetail_explain(options);
      } else {
        this.showDetail_search(options);
      }
    }
  },
  onShow() {
    this.showStart();
  },

  onHide() {
    this.exit();
  },
  onUnload() {
    this.exit();
  },
  onShareAppMessage: function() {
    var page = wx.getStorageSync("share_player_page");
    var key1 = wx.getStorageSync("share_player_list");
    var key2 = wx.getStorageSync("share_player_video");
    var path = `/pages/player/main?share=true&page=${page}&key1=${key1}&key2=${key2}&title=${
      this.title
    }`;
    return {
      title: this.title,
      path: path,
      imageUrl: this.shareImg,
      success(res) {
        console.log("转发成功!", res, path);
      },
      fail(res) {
        console.log("转发失败!", res);
      }
    };
  }
};
</script>

<style scoped lang='scss'>
.grid-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.grid-var {
  flex: 1;
}

.content {
  height: 100%;
}

.listBox {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  height: 500rpx;
}

.videoBox {
  width: 737rpx;
  height: 423rpx;
  margin: 0 auto;
  video {
    width: 100%;
    height: 100%;
  }
  .fixedVideo {
    position: fixed;
    top: 0;
    z-index: 100;
    width: 750rpx;
    height: 423rpx;
    left: 0;
    right: 0;
  }
}

.listBoxTotal {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  transition-timing-function: ease-in;
  -webkit-transform: translate3d(0, 0, 0);
  /*开启硬件加速*/
  transform: translateY(0);
  transition: transform 500ms;
  position: absolute;
  top: 0;
  z-index: 200;
}

.searchBox {
  // position: absolute;
  top: 0;
  z-index: 100;
  width: 100%;
}

.listHide {
  transform: translateY(100%);
}

.contentBox {
  position: relative;
}
</style>



